.container {
  text-align: center;
}

.form {
  padding: 24px 16px;
  margin: 50px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
  height: 300px;
  border: 1px solid grey;
  border-radius: 8px;
}

.input {
  border-radius: 8px;
  border: 1px solid grey;
  padding: 0 16px;
  display: inline-block;
  width: calc(100% - 32px);
  line-height: 40px;
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }
}

.button {
  border: none;
  border-radius: 8px;
  margin-top: auto;
  width: 100%;
  height: 40px;
  font-size: 16px;
  font-weight: bold;
  background-color: chocolate;
}

.error {
  color: red;
  font-size: 12px;
  font-weight: bold;
}

.link {
  color: orangered;
  display: inline-block;
  text-decoration: none;
  padding: 8px 0;
}